"use client"

import { useEffect, useState } from "react";
import api from "../../lib/axios";
import Link from "next/link";

export default function PostsPage() {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchPosts = async () => {
  setLoading(true);
  try {
    const token = localStorage.getItem("token");
    const res = await fetch("/api/proxy/posts", {
      method: "GET",
      headers: {
        "Authorization": `Bearer ${token}`,
      },
    });

    if (!res.ok) throw new Error("Failed to fetch posts");
    const data = await res.json();
    setPosts(data);
  } catch (err) {
    alert(err.message);
  } finally {
    setLoading(false);
  }
};


  useEffect(() => { fetchPosts(); }, []);

  console.log(posts);
  if (loading) return <div className="text-center mt-20">Loading...</div>;

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Posts</h1>
        <Link href="/posts/create" className="btn btn-primary">Create New</Link>
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {posts.map(post => (
          <div key={post.id} className="card bg-base-100 shadow-xl p-4">
            <h2 className="card-title">{post.title}</h2>
            <p>{post.body?.slice(0, 100)}...</p>
            <div className="card-actions justify-end mt-2">
              <Link href={`/posts/${post.id}`} className="btn btn-sm btn-outline">View</Link>
              <Link href={`/posts/${post.id}/edit`} className="btn btn-sm btn-secondary">Edit</Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
